.mobile {
  position: relative;
  // background-color: #343a40;
  height: 70px;
  padding: 10px 0;
  width: 100%;

  @media screen and (min-width: 777.98px) {
    display: none;
  }
}

.navWrapper {
  position: absolute;
  z-index: 50;
  top: 70px;
  right: 0;
  transform: translateX(-100%) skew(50deg, 50deg);

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  padding-left: 10px;
  display: flex;
  flex-direction: column;

  justify-content: space-evenly;

  width: 100%;
  height: 400px;
  text-decoration: none;

  gap: 20px;
  // background-color: #343a40;
  border-top: 2px solid black;
}

.navWrapperIsOpen {
  position: absolute;
  z-index: 50;
  top: 70px;
  right: 0;
  transform: translateX(0);
  transition: transform 0.2s linear;
  padding-left: 10px;
  display: flex;
  flex-direction: column;

  justify-content: space-evenly;

  width: 100%;
  height: 400px;
  text-decoration: none;

  gap: 20px;
  background-color: #343a40;
  border-top: 2px solid black;
}

.logo {
  width: 100px;
}

.li {
  text-decoration: none;
  color: white;
  font-size: 20px;

  &:hover,
  &:focus {
    color: #ffc107;
  }
}

.active {
  border-bottom: 4px solid #ffc107;
  width: 80px;
}

.burger {
  position: absolute;
  right: 5%;
  top: 24%;
  cursor: pointer;
}
